<template>
  <div class="useradd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>
          {{title}}
          <v-spacer></v-spacer>
          <v-switch label="Hidden" color="blue" v-model="list.hidden" value="true" hide-details></v-switch>
        </v-card-title>

        <v-card-text class="mt-3">
          <!-- 表單內容 -->
          <v-form ref="form">
            <v-alert color="primary" border="left" outlined>
              <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                <h2>個人基本資料</h2>
              </v-sheet>
              <v-row>
                <v-col cols="12" md="9">
                  <v-row>
                    <v-col cols="12" sm="4">
                      <v-text-field
                        label="病歷號"
                        v-model="list.p_code"
                        :rules="emptyRules"
                        ref="p_code"
                        readonly
                        outlined
                        v-if="processType=='add'"
                      ></v-text-field>
                      <v-text-field
                        label="病歷號"
                        v-model="list.p_code"
                        :rules="emptyRules"
                        ref="p_code"
                        v-if="processType=='edit'"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="4">
                      <v-text-field
                        label="姓名"
                        v-model="list.name"
                        :rules="emptyRules"
                        @keyup="checkName"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="4">
                      <v-select :items="sexitems" label="性別" v-model="list.sex"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-checkbox label="公費資格" v-model="list.publicexpenses" value="true"></v-checkbox>
                    </v-col>
                    <v-col cols="12" sm="4">
                      <v-text-field label="公費資格內容" v-model="list.publicexpenses_content"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-checkbox label="DNR" v-model="list.dnr" value="true"></v-checkbox>
                    </v-col>
                    <v-col cols="12" sm="4">
                      <v-text-field label="DNR內容" v-model="list.dnr_content"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <datepicker :label="'入住日期'" v-model="list.in_date"></datepicker>
                    </v-col>
                    <v-col cols="12" sm="3" class="d-flex">
                      <datepicker :label="'退住日期'" v-model="list.out_date"></datepicker>
                      <v-icon @click="list.out_date = '0000-00-00'">mdi-window-close</v-icon>
                      <!-- <v-text-field label="退住日期" type="date" v-model="list.out_date"></v-text-field> -->
                    </v-col>
                    <v-col cols="12" sm="3" class="d-flex">
                      <datepicker :label="'出生日期'" v-model="list.birthday"></datepicker>
                      <v-icon @click="list.birthday = '0000-00-00'">mdi-window-close</v-icon>
                      <!-- <v-text-field label="出生日期" type="date" v-model="list.birthday"></v-text-field> -->
                    </v-col>
                    <v-col cols="12" sm="1">
                      <v-text-field label="年齡" :value="age()" readonly></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="2" class="d-flex align-center">
                      <v-text-field label="身分證字號" v-model="list.id_num" counter="10" :readonly="idEdit && processType=='edit'"></v-text-field>
                      <v-btn class="success" small @click.stop="idEdit = false" v-if="processType=='edit'">修改</v-btn>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-select :items="sourceitems" label="入住來源" v-model="list.source"></v-select>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-text-field label="入住來源-其他內容" v-model="list.source_content"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-select :items="enterfnitems" label="入院方式" v-model="list.enterfn"></v-select>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-text-field label="入院方式-其他內容" v-model="list.enterfn_content"></v-text-field>
                    </v-col>
                    <!-- <v-col cols="12" sm="3">
                      <v-combobox :items="obstacleItems" label="障礙類別" v-model="list.obstacle"></v-combobox>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-combobox :items="obstaclelvItems" label="障礙等級" v-model="list.obstaclelv"></v-combobox>
                    </v-col>
                    <v-col cols="12" sm="3" class="d-flex">
                      <datepicker :label="'到期日'" v-model="list.obstacledate"></datepicker>
                      <v-icon @click="list.obstacledate = '0000-00-00'">mdi-window-close</v-icon>
                    </v-col>-->
                  </v-row>
                </v-col>
                <v-col cols="12" md="3" v-if="processType=='edit'">
                  <input type="file" ref="fileInput" @change="onFileSelected" style="display:none" />
                  <v-img
                    :src="this.$store.state.base_url+'upload/thumb/'+list.pic_url"
                    :lazy-src="this.$store.state.base_url+'upload/thumb/lazypic.jpg'"
                    aspect-ratio="0.9"
                    class="grey lighten-2 d-flex align-end"
                  >
                    <v-progress-circular color="white" indeterminate size="32" v-if="uploading"></v-progress-circular>
                    <v-btn
                      icon
                      class="success ma-1 float-right"
                      @click.stop="uploadId=list.snkey;uploadFolder='thumb';$refs.fileInput.click()"
                      v-if="processType=='edit'"
                    >
                      <v-icon class="white--text">mdi-cloud-upload</v-icon>
                    </v-btn>
                  </v-img>
                </v-col>

                <v-col cols="12">
                  <v-alert color="primary" outlined>
                    <header>障礙設定</header>
                    <v-row>
                      <v-col cols="12" sm="3">
                        <v-select :items="obstacleItems" label="障礙類別" v-model="obstacle_kind"></v-select>
                      </v-col>
                      <v-col cols="12" sm="3">
                        <v-select :items="obstaclelvItems" label="障礙等級" v-model="obstacle_lv"></v-select>
                      </v-col>
                      <v-col cols="12" sm="2" class="d-flex">
                        <datepicker :label="'到期日'" v-model="obstacle_date"></datepicker>
                        <v-icon @click="obstacle_date = '0000-00-00'">mdi-window-close</v-icon>
                      </v-col>
                      <v-col cols="12" sm="2">
                        <v-btn class="primary" block @click="addObstacle">新增障礙設定</v-btn>
                      </v-col>
                      <v-col cols="12" sm="2">
                        <v-btn class="primary" block @click="addObstacle('forever')">新增障礙設定(無限期)</v-btn>
                      </v-col>
                      <v-col cols="12">
                        已設定內容:
                        <v-chip
                          class="ma-2"
                          color="primary"
                          v-for="(oitem,index) in obstacleArr"
                          :key="index"
                        >
                          {{oitem.kind}} - {{oitem.lv}} - {{oitem.date}}
                          <v-icon color="red" @click="delObstacle(index)">mdi-close</v-icon>
                        </v-chip>
                      </v-col>
                    </v-row>
                  </v-alert>
                </v-col>

                <v-col cols="12" sm="9">
                  <header>語言</header>
                  <v-row justify="space-around">
                    <v-checkbox label="國語" v-model="list.language1" value="true"></v-checkbox>
                    <v-checkbox label="台語" v-model="list.language2" value="true"></v-checkbox>
                    <v-checkbox label="客家語" v-model="list.language3" value="true"></v-checkbox>
                    <v-checkbox label="筆談" v-model="list.language4" value="true"></v-checkbox>
                    <v-checkbox label="圖片" v-model="list.language5" value="true"></v-checkbox>
                    <v-checkbox label="手勢" v-model="list.language6" value="true"></v-checkbox>
                    <v-checkbox label="其他" v-model="list.language7" value="true"></v-checkbox>
                  </v-row>
                </v-col>
                <v-col cols="12" sm="3">
                  <v-text-field label="其他內容" v-model="list.language8"></v-text-field>
                </v-col>
                <v-col cols="12" sm="9">
                  <header>婚姻狀況</header>
                  <v-row justify="space-around">
                    <v-checkbox label="未婚" v-model="list.marry1" value="true"></v-checkbox>
                    <v-checkbox label="已婚" v-model="list.marry2" value="true"></v-checkbox>
                    <v-checkbox label="離婚" v-model="list.marry3" value="true"></v-checkbox>
                    <v-checkbox label="喪偶" v-model="list.marry4" value="true"></v-checkbox>
                    <v-checkbox label="分居" v-model="list.marry5" value="true"></v-checkbox>
                    <v-checkbox label="其他" v-model="list.marry6" value="true"></v-checkbox>
                  </v-row>
                </v-col>
                <v-col cols="12" sm="3">
                  <v-text-field label="其他內容" v-model="list.marry7"></v-text-field>
                </v-col>
                <v-col cols="12" sm="2">
                  <v-select :items="faithitems" label="宗教信仰" v-model="list.faith"></v-select>
                </v-col>
                <v-col cols="12" sm="2">
                  <v-text-field label="身高(公分)" v-model="list.height"></v-text-field>
                </v-col>
                <v-col cols="12" sm="2">
                  <v-text-field label="體重(公斤)" v-model="list.weight"></v-text-field>
                </v-col>
                <v-col cols="12" sm="2">
                  <v-text-field label="平日就醫地點" v-model="list.usual_hospital"></v-text-field>
                </v-col>
                <v-col cols="12" sm="4">
                  <v-text-field label="保管物品" v-model="list.keepthings"></v-text-field>
                </v-col>
              </v-row>
            </v-alert>

            <v-alert color="primary" border="left" outlined>
              <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                <h2>連絡人資料</h2>
              </v-sheet>
              <v-row>
                <v-col cols="12">
                  <v-btn class="error" small @click.stop="newContact">新增連絡人</v-btn>
                </v-col>

                <v-col cols="12" v-for="(cman,index) in list.cman" :key="index" class="my-0 py-0">
                  <v-row>
                    <v-col cols="12" sm="2" class="my-0 py-0">
                      <v-text-field
                        :label="'緊急連結人'+index"
                        v-model="list.cman[index]"
                        prepend-icon="mdi-cancel"
                        @click:prepend="delContact(index)"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="2" class="my-0 py-0">
                      <v-text-field :label="'與住民之關係'+index" v-model="list.crelationship[index]"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="2" class="my-0 py-0">
                      <v-text-field :label="'電話'+index" v-model="list.cphone[index]"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" class="my-0 py-0">
                      <v-text-field :label="'地址'+index" v-model="list.caddress[index]"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="12" class="my-0 py-0">
                      <v-text-field :label="'備註'+index" v-model="list.cnote[index]"></v-text-field>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-alert>

            <v-alert color="primary" border="left" outlined>
              <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                <h2>個人病史</h2>
              </v-sheet>

              <v-row>
                <v-col cols="12" class="d-flex">
                  <header>入住診斷</header>
                  <v-btn class="error ml-2" small @click.stop="newDiagnosis">新增病名</v-btn>
                </v-col>
                <v-col
                  cols="12"
                  v-for="(item,index) in list.diagnosis"
                  :key="index"
                  class="ma-0 py-0"
                >
                  <div class="d-flex align-center">
                    <v-combobox
                      :label="'疾病診斷'+index"
                      :items="DiagnosisItems"
                      prepend-icon="mdi-cancel"
                      @click:prepend="delUserDiagnosis(index)"
                      v-model="list.diagnosis[index]"
                      @focus="disabled = false"
                      @blur="disabled=true"
                    ></v-combobox>
                    <v-icon
                      color="success"
                      @click.stop="addDiagnosis(index)"
                      :disabled="!disabled"
                    >mdi-plus-circle</v-icon>
                    <v-icon
                      color="error"
                      @click.stop="delDiagnosis(index)"
                      :disabled="!disabled"
                    >mdi-minus-circle</v-icon>
                  </div>
                </v-col>

                <v-col cols="12">
                  <v-divider></v-divider>
                </v-col>

                <v-col cols="12">
                  <header>過去病史</header>
                  <v-row>
                    <v-col cols="12">
                      <h5>1.曾患疾病</h5>
                      <v-row>
                        <v-col cols="12">
                          <v-text-field label="曾患疾病" v-model="list.hadadisease"></v-text-field>
                        </v-col>
                      </v-row>

                      <v-row>
                        <v-col cols="12" sm="1">
                          <v-select :items="ynitems" label="2.住院次數" v-model="list.behospitalized1"></v-select>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field label="次" v-model="list.behospitalized2"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="2">
                          <v-text-field label="原因" v-model="list.behospitalized3"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="2">
                          <v-text-field label="住院地點" v-model="list.behospitalized4"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-select
                            :items="ynitems"
                            label="3.手術情形"
                            v-model="list.surgerysituation1"
                          ></v-select>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field label="次" v-model="list.surgerysituation2"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="2">
                          <v-text-field label="原因" v-model="list.surgerysituation3"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="2">
                          <v-text-field label="手術地點" v-model="list.surgerysituation4"></v-text-field>
                        </v-col>

                        <v-col cols="12" sm="2">
                          <v-select :items="ynitems" label="4.輸血經驗" v-model="list.bloodtransfusion"></v-select>
                        </v-col>
                        <v-col cols="12" sm="2">
                          <h5>不良反應</h5>
                          <v-row justify="space-around">
                            <v-checkbox label="無" v-model="list.adversereactions1" value="true"></v-checkbox>
                            <v-checkbox label="有" v-model="list.adversereactions2" value="true"></v-checkbox>
                          </v-row>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-select :items="ynitems" label="5.服藥" v-model="list.takingmedicine1"></v-select>
                        </v-col>
                        <v-col cols="12" sm="3">
                          <v-text-field label="藥物名稱及使用情形" v-model="list.takingmedicine2"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-select
                            :items="ynitems"
                            label="6.先天疾病"
                            v-model="list.congenitaldisease1"
                          ></v-select>
                        </v-col>
                        <v-col cols="12" sm="3">
                          <v-text-field label="疾病名稱" v-model="list.congenitaldisease2"></v-text-field>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                </v-col>

                <v-col cols="12">
                  <v-divider></v-divider>
                </v-col>

                <v-col cols="12">
                  <header>過敏史</header>
                  <v-row>
                    <v-col cols="12" sm="2">
                      <v-select
                        :items="allergyhistory1items"
                        label="1.藥物"
                        v-model="list.allergyhistory1"
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-text-field label="藥名" v-model="list.allergyhistory2"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select
                        :items="allergyhistory3items"
                        label="2.食物"
                        v-model="list.allergyhistory3"
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-text-field label="食物名" v-model="list.allergyhistory4"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-text-field label="3.其他" v-model="list.allergyhistory5"></v-text-field>
                    </v-col>
                  </v-row>
                </v-col>

                <v-col cols="12">
                  <v-divider></v-divider>
                </v-col>

                <v-col cols="12">
                  <header>輔助器材</header>
                  <v-row justify="space-around" class="mx-2">
                    <v-checkbox label="無" v-model="list.auxiliaryequipment1" value="true"></v-checkbox>
                    <v-checkbox label="助聽器" v-model="list.auxiliaryequipment2" value="true"></v-checkbox>
                    <v-checkbox label="活動假牙" v-model="list.auxiliaryequipment3" value="true"></v-checkbox>
                    <v-checkbox label="義眼" v-model="list.auxiliaryequipment4" value="true"></v-checkbox>
                    <v-checkbox label="眼鏡" v-model="list.auxiliaryequipment5" value="true"></v-checkbox>
                    <v-checkbox label="隱形眼鏡" v-model="list.auxiliaryequipment6" value="true"></v-checkbox>
                    <v-checkbox label="義肢:上肢左" v-model="list.auxiliaryequipment7" value="true"></v-checkbox>
                    <v-checkbox label="義肢:上肢右" v-model="list.auxiliaryequipment8" value="true"></v-checkbox>
                    <v-checkbox label="義肢:下肢左" v-model="list.auxiliaryequipment9" value="true"></v-checkbox>
                    <v-checkbox label="義肢:下肢右" v-model="list.auxiliaryequipment10" value="true"></v-checkbox>
                    <v-checkbox label="矯正鞋" v-model="list.auxiliaryequipment11" value="true"></v-checkbox>
                    <v-checkbox label="輪椅" v-model="list.auxiliaryequipment12" value="true"></v-checkbox>
                    <v-checkbox label="助行器" v-model="list.auxiliaryequipment13" value="true"></v-checkbox>
                    <v-checkbox label="拐杖" v-model="list.auxiliaryequipment14" value="true"></v-checkbox>
                    <v-checkbox label="手杖" v-model="list.auxiliaryequipment15" value="true"></v-checkbox>
                    <v-checkbox label="假髮" v-model="list.auxiliaryequipment16" value="true"></v-checkbox>
                  </v-row>
                </v-col>
              </v-row>
            </v-alert>

            <v-alert color="primary" border="left" outlined>
              <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                <h2>主要護理問題</h2>
              </v-sheet>

              <v-textarea label="主要護理問題" v-model="list.mainnursingissues"></v-textarea>
            </v-alert>

            <v-alert color="primary" border="left" outlined v-if="processType=='edit'">
              <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                <h2>家族圖譜</h2>
              </v-sheet>
              <v-img
                :src="this.$store.state.base_url+'upload/familytree/'+list.pic_url_familytree"
                :lazy-src="this.$store.state.base_url+'upload/familytree/lazypic.jpg'"
                class="grey lighten-2"
                height="500px"
                contain
              >
                <v-progress-circular color="white" indeterminate size="32" v-if="uploading"></v-progress-circular>
                <v-btn
                  icon
                  class="success ma-1 float-right"
                  @click.stop="uploadId=list.snkey;uploadFolder='familytree';$refs.fileInput.click()"
                  v-if="processType=='edit'"
                >
                  <v-icon class="white--text">mdi-cloud-upload</v-icon>
                </v-btn>
              </v-img>
            </v-alert>

            <v-alert color="primary" border="left" outlined v-if="processType=='edit'">
              <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                <h2>訪視設定</h2>
              </v-sheet>
              <v-checkbox v-model="list.visitLimit" label="限制訪客"></v-checkbox>
              <v-textarea v-model="list.visitLimitContent" label="限制內容" v-show="list.visitLimit"></v-textarea>
            </v-alert>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="primary"
            @click="addOK"
            v-if="processType=='add'"
            :loading="loading"
            :disabled="loading"
          >確認新增</v-btn>
          <v-btn
            class="success"
            @click="editOK"
            v-if="processType=='edit' && !$store.state.pData.pharmacist_key"
            :loading="loading"
            :disabled="loading"
          >確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import datepicker from "@/components/DatePicker";
import dayjs from "dayjs";

export default {
  components: { datepicker },
  // props: ["items"],
  data() {
    return {
      dialog: false,
      list: {
        in_date: new Date().toISOString().substr(0, 10),
        cman: { 1: "" },
        crelationship: { 1: "" },
        cphone: { 1: "" },
        caddress: { 1: "" },
        cnote: { 1: "" },
        diagnosis: { 1: "" },
      }, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      disabled: true, //病名新增及刪除的按鈕開關
      sexitems: ["男", "女"],
      DiagnosisItems: [], //病名資料
      sourceitems: ["自由登記", "醫院轉介", "家屬介紹", "社衛政轉介" , "其他"],
      enterfnitems: ["步行", "輪椅", "輪椅", "扶持", "其他"],
      // take_medicine5items: ["是", "否"],
      allergyhistory1items: ["是", "否"],
      allergyhistory3items: ["是", "否"],
      ynitems: ["無", "有"],
      faithitems: ["佛教", "道教", "回教", "基督教", "天主教", "其他"],
      obstacleItems: [
        "第一類",
        "第二類",
        "第三類",
        "第四類",
        "第五類",
        "第六類",
        "第七類",
        "第八類",
      ],
      obstaclelvItems: ["輕度", "中度", "重度", "極重度"],

      obstacleArr: [],
      obstacle_kind: "",
      obstacle_lv: "",
      obstacle_date: "",

      selectedFile: "", //選擇的上傳檔案資料
      uploadId: "", //上傳檔案後的回傳ID
      uploadFolder: "", //暫存上傳檔案的種類 (病歷的大頭貼user或家族樹 familytree)
      fd: new FormData(), //上傳的檔案及文案內容
      uploading: false, //控制上傳中的動畫

      loading: false, //處理新增/修改/刪除按鈕讀取時的動畫設定 , 同時控制disabled
      userItems: [], //暫存目前全部的使用者資料
      idEdit:true, //身分證字號是否可以修改
    };
  },

  async mounted() {
    await this.getDiagnosis();
    await this.getUserItems();

    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },

  methods: {
    //新增障礙設定
    addObstacle(type = null) {
      if (type == "forever") {
        if (!!this.obstacle_kind && !!this.obstacle_lv) {
          this.obstacleArr.push({
            kind: this.obstacle_kind,
            lv: this.obstacle_lv,
            date: '無限期',
          });
        }
      } else {
        if (
          !!this.obstacle_kind &&
          !!this.obstacle_lv &&
          !!this.obstacle_date &&
          this.obstacle_date != "0000-00-00"
        ) {
          this.obstacleArr.push({
            kind: this.obstacle_kind,
            lv: this.obstacle_lv,
            date: this.obstacle_date,
          });
        }
      }
    },
    //刪除障礙設定
    delObstacle(index) {
      this.obstacleArr.splice(index, 1);
    },
    age() {
      return (
        parseInt(dayjs().format("YYYY")) -
        parseInt(dayjs(this.list.birthday).format("YYYY"))
      );
    },
    checkName() {
      this.userItems.find((i) => {
        if (i.name == this.list.name) {
          var pop = {
            msg: "姓名重覆!!請再確認!!",
            type: true,
            theme: "warning",
          };
          this.$store.commit("snackbar", pop);
        }
      });
    },
    async getUserItems() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      await this.axios
        .post("user/getAll/" + this.$store.state.databaseName)
        .then((rs) => {
          this.userItems = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off

          //病歷號自動生成-取出病歷最大值+1;不對的時候再自行修改
          this.create_p_code();
        });
    },
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增病歷";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {
        p_code: "",
        in_date: new Date().toISOString().substr(0, 10),
        cman: { 1: "" },
        crelationship: { 1: "" },
        cphone: { 1: "" },
        caddress: { 1: "" },
        cnote: { 1: "" },
        diagnosis: { 1: "" },
      };

      // //病歷號自動生成-取出病歷最大值+1;不對的時候再自行修改
      this.create_p_code();
    },
    create_p_code() {
      var matchData = this.userItems.map((i) => {
        if (i.p_code.match(/[\uff00-\uffff]/g) == null) {
          return parseInt(i.p_code);
        } else {
          return 0;
        }
      });
      
      this.list.p_code = `000${Math.max(...matchData) + 1}`;
    },
    onFileSelected(event) {
      //選取圖片
      this.selectedFile = event.target.files[0];
      var extension = this.selectedFile.name.split(".").pop().toLowerCase(); //取副檔名
      var fitType = ["gif", "png", "jpg", "jpeg"];

      var errorMsg = ""; //判斷檔案是否為圖檔及檔案大小
      if (fitType.indexOf(extension) == -1) {
        errorMsg = "檔案:" + this.selectedFile.name + "不是支持的影像檔案";
      }
      if (this.selectedFile.size > 1024 * 1024 * 5) {
        //檔案大小設定-最大5MB
        errorMsg = "檔案:" + this.selectedFile.name + "不能超過5M唷";
      }

      if (errorMsg == "") {
        this.onUpload();
      } else {
        var pop = { msg: errorMsg, type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
        this.selectedFile = "";
      }
    },
    onUpload() {
      this.uploading = true;
      //執行上傳
      this.fd.append("snkey", this.uploadId); //放入上傳圖檔的id
      this.fd.append("folder", this.uploadFolder); //放入上傳圖檔的種類 (user / familytree)
      this.fd.append("image", this.selectedFile);

      this.axios
        .post(
          "general/upload/" + this.$store.state.databaseName + "/user",
          this.fd
        )
        .then((rs) => {
          if (this.uploadFolder == "thumb") {
            this.oldList.pic_url = this.list.pic_url = rs.data.success;
          }
          if (this.uploadFolder == "familytree") {
            this.oldList.pic_url_familytree = this.list.pic_url_familytree =
              rs.data.success;
          }
          this.uploading = false;
        });
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改病歷資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容

      if (!!this.list.obstacle) {
        this.obstacleArr = this.list.obstacle;
      } else {
        this.obstacleArr = [];
      }

      if (this.list.pic_url == "") {
        this.list.pic_url = "lazypic.jpg";
      }

      if (this.list.out_date == "0000-00-00") {
        this.list.out_date = "";
      }

      if (this.list.birthday == "0000-00-00") {
        this.list.birthday = "";
      }

      if (this.list.obstacledate == "0000-00-00") {
        this.list.obstacledate = "";
      }
    },
    //確認新增
    addOK() {
      // 2023.4.20 加入新增時如果還在loading中,則跳離,避免新增時連按
      if (this.loading){
        console.log('rePush')
        return false;
      }

      if (this.$refs.form.validate()) {
        var repeat = "";
        //判斷病歷號,身分證是否重覆
        repeat = this.userItems.find((item) => {
          if (item.p_code == this.list.p_code) {
            var pop = {
              msg: "病歷號重覆!!請再確認!!",
              type: true,
              theme: "warning",
            };
            this.$store.commit("snackbar", pop);
            return item;
          }
          if (item.id_num == this.list.id_num) {
            var pop = {
              msg: "身分證字號重覆!!請再確認!!",
              type: true,
              theme: "warning",
            };
            this.$store.commit("snackbar", pop);
            return item;
          }
        });

        if (repeat != undefined) {
          return false;
        }
        //判斷統編是否重覆 end

        this.$store.commit("getTime"); //取得現在時間
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容
        this.list.obstacle = JSON.stringify(this.obstacleArr);

        this.loading = true;
        this.axios
          .post(
            "user/add/" + this.$store.state.databaseName,
            qs.stringify(this.list)
          )
          .then((rs) => {
            if (rs.data.state == "p_code_exist") {
              // alert('重新處理病歷號');
              this.getUserItems();
              this.loading = false;
              var pop = {
                msg: "病歷號已存在,已重新生成病歷號;請重新按新增鈕",
                type: true,
                theme: "warning",
              };
              this.$store.commit("snackbar", pop);
            }

            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "error" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
              // this.$refs.form.resetValidation();
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完整,請再確認",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        //判斷統編是否重覆
        var repeat = this.userItems.find((item) => {
          return item.p_code == this.list.p_code;
        });

        if (repeat.length > 0 && this.oldList.p_code != this.list.p_code) {
          var pop = {
            msg: "病歷號重覆!!請再確認!!",
            type: true,
            theme: "warning",
          };
          this.$store.commit("snackbar", pop);
          return false;
        }
        //判斷統編是否重覆 end

        this.$store.commit("getTime"); //取得現在時間
        this.list["edit_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容
        this.list.obstacle = JSON.stringify(this.obstacleArr);

        this.axios
          .post(
            "user/edit/" + this.$store.state.databaseName,
            qs.stringify(this.list)
          )
          .then((rs) => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              this.list.obstacle = this.obstacleArr; //要加這行.不然直接再點修改會出現錯誤

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }

              this.$emit("getALLUser");
            }
          });
      }
    },
    //其他單頁功能
    async getDiagnosis() {
      //取得所有疫病名稱
      await this.axios
        .post("general/getAll/" + this.$store.state.databaseName + "/diagnosis")
        .then((rs) => {
          this.DiagnosisItems = rs.data.map((item) => {
            return item.name;
          });
        });
    },
    newContact() {
      //新增聯絡人
      var count = (Object.keys(this.list.cman).length + 1).toString();
      this.$set(this.list.cman, count, "");
      this.$set(this.list.crelationship, count, "");
      this.$set(this.list.cphone, count, "");
      this.$set(this.list.caddress, count, "");
      this.$set(this.list.cnote, count, "");
    },
    delContact(index) {
      //刪除聯絡人
      this.$delete(this.list.cman, index);
    },
    newDiagnosis() {
      //新增病名
      var count = (Object.keys(this.list.diagnosis).length + 1).toString();
      this.$set(this.list.diagnosis, count, "");
    },
    delUserDiagnosis(index) {
      //刪除使用者的病名
      this.$delete(this.list.diagnosis, index);
    },
    addDiagnosis(index) {
      //新增病名
      var name = this.list.diagnosis[index];
      if (name == "" || name == null) {
        var pop = { msg: "未輸入病名" + index, type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
        return false;
      }

      this.$store.commit("getTime"); //取得現在時間
      var postData = {
        name: name, //病名
        create_man:
          this.$store.state.pData.username + "(" + this.$store.state.time + ")", //新增的個人資料及時間
      };

      this.axios
        .post(
          "general/addCheckRepeat/" +
            this.$store.state.databaseName +
            "/diagnosis",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data == "repeat") {
            //重覆
            var pop = { msg: "病名已存在!!", type: true, theme: "warning" };
            this.$store.commit("snackbar", pop);
          } else {
            //新增
            var pop = { msg: "病名已新增!!", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.getDiagnosis();
          }
        });
    },
    delDiagnosis(index) {
      //刪除病名
      var name = this.list.diagnosis[index];
      if (name == "" || name == null) {
        var pop = { msg: "未輸入病名" + index, type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
        return false;
      }

      this.$store.commit("getTime"); //取得現在時間
      var postData = {
        name: name, //病名
        info:
          this.$store.state.pData.username +
          "(" +
          this.$store.state.time +
          ")(" +
          name +
          ")", //執行動作的人,時間,內容
      };

      this.axios
        .post(
          "general/delCheckExist/" +
            this.$store.state.databaseName +
            "/diagnosis",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data == "noData") {
            //不存在的資料
            var pop = { msg: "病名不存在!!", type: true, theme: "warning" };
            this.$store.commit("snackbar", pop);
          } else {
            //刪除
            var pop = { msg: "病名已刪除!!", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.getDiagnosis();
          }
        });
    },
  },
};
</script>

